//----------------------------------------
// Colors

$orange: #ff623f; // bg & link for light text + text for light bg
$green: #225560; // button for light text
// $white already defined in bootstrap // text for dark bg
$white-transparent: rgba(255, 255, 255, .2); // border for header & footer
$gray-f0: #f0f0f0; // bg for dark text
$gray-ad: #adb5bd; // border for light bg
$black: #000; // bg for light text + text for light bg

// generate colors for .bg-, .border-, .btn-, .text-
// appended to bootstrap default ones
$theme-colors: (
    'orange': $orange,
    'green': $green,
    'white-transparent': $white-transparent,
    'gray-f0': $gray-f0,
    'gray-ad': $gray-ad,
    'black': $black,
);

$yiq-contrasted-threshold: 195;

//----------------------------------------
// RFS

$enable-responsive-font-sizes: true;
$rfs-base-font-size: 1rem;
$rfs-factor: 3;

//----------------------------------------
// Spacing

$spacer: 1rem;

$spacers: (
    '15s': $spacer * 0.15,
    '25s': $spacer * 0.25,
    '50s': $spacer * 0.5,
    '75s': $spacer * 0.75,
    '100s': $spacer,
    '125s': $spacer * 1.25,
    '150s': $spacer * 1.5,
    '200s': $spacer * 2,
    '250s': $spacer * 2.5,
    '300s': $spacer * 3,
    '400s': $spacer * 4,
    '500s': $spacer * 5,
);

$sizes: (
    10: 10%,
);

//----------------------------------------
// Typography

// cross-check font-weights with web font in default.html

// paragraphs
$font-family-base: 'IBM Plex Sans', sans-serif;
$font-size-base: 1.1rem; // body font size
$font-weight-normal: 400;
$font-weight-bold: 500;
$font-weight-bolder: $font-weight-bold;
$lead-font-size: $font-size-base * 1.2;
$lead-font-weight: 400;
$small1-font-size: $font-size-base * 0.9;
$small2-font-size: $font-size-base * 0.8;
$line-height-base: 1.6;
$paragraph-margin-bottom: 0;

// headings
$headings-font-family: 'Open Sans', sans-serif;
$display1-size: $font-size-base * 2.3;
$display2-size: $font-size-base * 2;
$display3-size: $font-size-base * 1.7;
$display4-size: $font-size-base * 1.2;
$display1-weight: 700;
$display2-weight: $display1-weight;
$display3-weight: 600;
$display4-weight: $display3-weight;
$h1-font-size: $font-size-base * 1.6;
$h2-font-size: $font-size-base * 1.2;
$h3-font-size: $font-size-base;
$headings-font-weight: 600;
$headings-line-height: 1.3;
$headings-margin-bottom: 0;

//--------------------------------------------------------------------------------

//----------------------------------------
// Body

$body-color: $black;

//----------------------------------------
// Buttons

$btn-font-family: $headings-font-family;
$btn-font-size: $font-size-base * 1.15;
$btn-font-weight: $headings-font-weight;
$btn-line-height: 1;
$btn-padding-y: spacer('100s');
$btn-padding-x: spacer('200s');

//----------------------------------------
// Links

$link-color: $orange;
$link-decoration: none;
$link-hover-color: darken($link-color, 15%);
$link-hover-decoration: $link-decoration;

//----------------------------------------
// Navbar

$navbar-padding-y: 0;
$navbar-padding-x: 0;

$navbar-toggler-padding-y: 0;
$navbar-toggler-padding-x: 0;

$navbar-light-color: #fff;
$navbar-light-toggler-icon-bg: str-replace(url("data:image/svg+xml,%3csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3e%3cpath stroke='#{$navbar-light-color}' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e"), "#", "%23");

//--------------------------------------------------------------------------------

@import '../../_vendor/bootstrap/4.3.1/stylesheets/variables';
