@import '../../_vendor/bootstrap/4.3.1/stylesheets/mixins';

// reset focus state back to browser default for form inputs
@mixin form-control-focus() {}

@mixin button-variant($background, $border,
                      $hover-background: darken($background, 10%)) {
    color: $white;
    background-color: $background;
    border-color: $background;
    @include hover {
        color: $white;
        background-color: $hover-background;
        border-color: $hover-background;
    }
}
