section.cta-intro {

    ul {
        @extend .row, .list-unstyled ;
        > li {
            @extend .col-12, .col-lg-6 ;
            > div + div { @extend .mt-250s ; }
            &.image { @extend .d-flex, .align-items-center, .justify-content-center ; }
            > img {
                max-width: 260px  ;
                @include media-breakpoint-up(lg) { max-width: 360px  ; }
                @extend .w-100, .mt-250s, .mt-lg-0 ;
            }
        }
    }

    div.component-header {
        @extend .text-lg-left ;
        h1 { @extend .display-1 ; }
        p { max-width: none ; }
    }

    div.component-link { @extend .text-lg-left ; }
    
}
