div.component-list {
    
    @extend .mt-0 ; // top margin set by li for multi-row lists

    h1 + p, p + .link { @extend .mt-100s ; }

    .link > a {
      &:after {
        display: inline-block ; 
        margin-left: .375rem ;
        transition: transform .2s ease ;
        content: '\2192' ; // rightwards arrow
      }
      &:hover:after { transform: translate3d(6px,0,0) ; }
    }
    
    h1 { @extend .display-4 ; }

    > ul {
        @extend .row, .list-unstyled ; // .col declared in html file
        > li {
            @extend .mt-250s ;
            > div {
                > h1, > div { @extend .mx-auto ; max-width: $para-max-width ; }
            }
            &.frames {
                > div { @extend .h-100, .p-200s, .border, .border-gray-ad, .rounded ; }
                h1 { line-height: $line-height-base; }
            }
            &.cards {
                > div { @extend .bg-white ; }
            }
            &.quotes {
                h1 { @extend .text-black ; }
            }
        }
    }

}
