div.component-article {

    h1 {
        & + p, & + ul, & + ol { @extend .mt-100s ; }
    }
    p, ul, ol {
        & + p, & + ul, & + ol { @extend .mt-100s ; }
        & + h1 { @extend .mt-200s ; }
    }
    li + li { @extend .mt-50s ; }
    
    h1, ul, ol { @extend .mx-auto ; max-width: $para-max-width ; }

    ul { padding-left: 20px ; } // override browser default of 40px
    
}
