//----------------------------------------
// Navbar
// xs-sm viewport. 1 row for logo link and x rows for nav items
// md+ viewport. 1 row for both logo link and nav items

div.component-nav-bar {
    ul {
        @extend .row, .list-unstyled, .text-center ;
        @extend .mt-150s, .mt-md-0 ;
        > li {
            @extend .col-12, .col-md ;
            &:not(:first-child) { @extend .mt-100s, .mt-md-0 ; }
        }
    }
    a {
        font-family: $headings-font-family ;
        @extend .small-1, .py-25s ;
    }
    .logo-link {
        @extend .link ;
        > a {
            @include font-size($display4-size) ;
            letter-spacing: 0.05rem ;
        }
    }
}

div.nav-bar-toggle {
    @extend .navbar, .navbar-light, .navbar-expand-lg ;
    ul { @extend .ml-auto, .mt-md-100s, .mt-lg-0 ; }
}

div.nav-bar-normal {
    @extend .d-flex, .align-items-center, .flex-column ;
    @extend .flex-md-row, .justify-content-md-between ;
}
