@import '../../_vendor/bootstrap/4.3.1/stylesheets/buttons';
// reset focus state back to browser default
.btn {
    &:focus, &.focus {
        outline: 1px dotted #212121;
        outline: 5px auto -webkit-browser-ring-color;
        box-shadow: unset;
    }
}

