div.component-form {

    @extend .text-center ;

    > form {
        @extend .mx-auto ;
        &.subscribe { max-width: 20rem ; }
        &.contact { max-width: $para-max-width ; }      
        > div {
            @extend .form-group ;
            input { @extend .form-control ; }
            textarea { @extend .form-control, .h-100 ; } // bootstrap 4 requires h-100 to use textarea rows
        }
        button { @extend .btn, .btn-green ; }
    }

}
