$para-max-width: 39rem ;

section {

    @extend .py-400s ;

    p { @extend .mx-auto ; max-width: $para-max-width ; }
    
    // a { @extend .text-decoration-none ; }
    .button > a { @extend .btn, .btn-green ; }
    .link > a {
        font-weight: $headings-font-weight ;
    }
    
    &.fg-light {
        h1 { @extend .text-white ; }
        p { @extend .text-white ; }
        a, li { @extend .text-white ; }
    }
    &.fg-dark {
        h1 { @extend .text-black ; }
    }

}
