section.footer {

    @extend .pt-200s, .pb-250s ;
    @extend .border-top, .border-white-transparent ;

    > div > ul {
        @extend .list-unstyled, .mt-150s, .mt-md-75s ;
        @extend .text-center, .text-md-left ;
        li {
            @extend .small-2, .text-white ;
            &:not(:first-child) { @extend .mt-75s ; }
        }
    }

}
